  margin-left: -50px;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

footer {
  text-align: center;
  color: skyblue;
}

.navbar{
  margin-bottom: 25px;
  padding: 5px;
  border: 1px black solid;
}

.h2{
  color: goldenrod;
}

.HomepageContent{
  padding: 130px;
  border: 3px black dashed;
  
}

.NotFoundDiv{
  background-color: peachpuff;
  padding: 20px;
}

.createblog{
  color: skyblue;
  background-color: peachpuff;
}


.links {
  text-decoration: none;
  margin-bottom: 25px;
  padding: 15px;
